@import 'main';

body {
    font-family: "Open Sans" sans-serif;
    font-size: 16px;
    font-weight: 400;
    text-decoration: none;
}


.container-all {
    background-color: #f6f2f2;

    .container-box {

        .header {
            background-color: $dark;

            &-menu {
                display: flex;
                justify-content: center;

                li {
                    padding: 43px 23px 33px;
                }
                a {
                    text-decoration: none;
                    color: #e0e0e0;
                    text-transform: uppercase;
                    font-size: 14px;
                    font-weight: 700;
                    line-height: 24px;
                    letter-spacing: 0.49px;
                }

                li:hover {
                    background-color: #ff3f40;
                }
            }
        }

        .intro {

            margin-top: 108px;
            padding-bottom: 111px;
            &-text {
                max-width: 588px;
                color: #000000;

                h1 {
                    font-family: "Open Sans - Extrabold" sans-serif;
                    font-size: 3.5rem;
                    font-weight: 700;
                    line-height: 72px;
                    text-transform: uppercase;
                    padding-bottom: 36px;
                }

                p {
                    opacity: 0.7;
                    font-family: "Open Sans";
                    line-height: 28px;
                    padding-bottom: 58px;
                    max-width: 520px;
                }

                .btn-cont {
                    margin-bottom: 18px;
                }
            }

            &-video {
                position: relative;
                padding-bottom: 56.25%;
                padding-top: 30px;
                height: 0;
                overflow: hidden;
                box-shadow: -2px -14px 73px rgba(0, 0, 0, 0.29);
            }

            &-video iframe {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
            }
        }
    }
}


.wrapper {
    padding-top: 110px;
    padding-bottom: 107px;
    border-bottom: 2px solid #ececec;

    &-text {

        h3 {
            opacity: 0.8;
            font-size: 42px;
            font-weight: 700;
            line-height: 55px;
            text-transform: uppercase;
            letter-spacing: 0.42px;
            padding-bottom: 50px;
        }
        p {
            font-weight: 400;
            line-height: 28px;
            padding-bottom: 58px;
        }

        &--btn {
            padding: 10px 60px;
            font-size: 18px;
            line-height: 34px;
            font-weight: 700;
            letter-spacing: 0.32px;
            border: 2px solid #ff2d2d;
            margin-bottom: 30px;
        }
    }

    &-cards {

        max-width: 600px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-content: center;

        .cards {
            max-width: 260px;
            position: relative;
            padding: 0 25px;

            h5 {
                padding-bottom: 15px;
                opacity: 0.7;
                font-family: "Open Sans - Semibold" sans-serif;
                font-size: 24px;
                font-weight: 400;
                letter-spacing: 0.27px;
                padding-top: 30px;
            }

            p {
                padding-bottom: 49px;
                opacity: 0.7;
                font-weight: 400;
                line-height: 28px;
                
            }
        }

        .cards-one:before {
            content: url('../img/cards/1.png');
            width: 44px;
            height: 44px; 
        }

        .cards-two:before {
            content: url('../img/cards/2.png');
            width: 44px;
            height: 44px;  
        }

        .cards-three:before {
            content: url('../img/cards/3.png');
            width: 44px;
            height: 44px;  
        }

        .cards-four:before {
            content: url('../img/cards/4.png');
            width: 44px;
            height: 44px;  
        }

        .cards-one {

        }

    }
}

.products {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 160px;

    &-tittle {
        max-width: 504px;
        
        h3 {
            opacity: 0.8;
            font-size: 42px;
            font-weight: 700;
            text-transform: uppercase;
            letter-spacing: 0.38px;

        }
        p {
            opacity: 0.7;
            font-weight: 400;
            line-height: 28px;
            position: relative;
        }

        p:after {
            content: '';
            width: 132px;
            height: 3px;
            background-color: #ff2d2d;
            position: absolute;
            top: 80px;
            left: 35%;
                        
        }
    }

    &-menu {
        width: 892px;
        padding-top: 88px;
        ul {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            li {

                a {
                    text-decoration: none;
                    opacity: 0.7;
                    color: #000000;
                    font-family: "Open Sans - Semibold" sans-serif;
                    font-size: 22px;
                    font-weight: 400;
                    line-height: 24px;
                    text-transform: uppercase;
                    letter-spacing: 0.22px;
                }

                a:hover {
                    color: #ff2d2d;
                }
            }
        }
    }

    &-cards {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        padding-top: 66px;
        padding-bottom: 138px;

        .item-2 {
            background-image: url('../img/people/2.png');
        }
        .item-3 {
            background-image: url('../img/people/3.png');
        }
        .item-4 {
            background-image: url('../img/people/4.png');
        }
        .item-5 {
            background-image: url('../img/people/5.png');
        }
        .item-6 {
            background-image: url('../img/people/6.png');
        }
        .item-7 {
            background-image: url('../img/people/7.png');
        }
        .item-8 {
            background-image: url('../img/people/8.png');
        }
        &-item {
            width: 250px;
            height: 250px;
            background-image: url('../img/people/1.png');
            background-position: center center;
            background-repeat: no-repeat;
            border: 1px solid #fff;
        }

        &-item:hover {
            border-radius: 10px;
            border: 1px solid #ececec;
            opacity: 0.6;
            cursor: pointer;
        }
    }

    &-team {
        text-align: left;
        padding-bottom: 105px;
        
        h3 {
            opacity: 0.8;
            font-size: 42px;
            font-weight: 700;
            text-transform: uppercase;
            letter-spacing: 0.38px;
        }
        p {
            opacity: 0.7;
            font-weight: 400;
            line-height: 28px;
            position: relative;
            
        }

        p:after {
            content: '';
            width: 132px;
            height: 3px;
            background-color: #ff2d2d;
            position: absolute;
            top: 140%;
            left: 0;
                        
        }
    }

    &-logo {
        max-width: 309px;
        &-home {
            width: 100%;
            height: 100%;
        }
    }
    &-info {
        text-align: left;
            h3 {
                padding-bottom: 35px;
                opacity: 0.8;
                font-size: 30px;
                text-transform: uppercase;
                letter-spacing: 0.28px;
                max-width: 630px;
            }

            p {
                opacity: 0.7;
                font-size: 16px;
                font-weight: 400;
                line-height: 28px;
                padding-bottom: 44px;
                max-width: 630px;
            }

            a {
                text-decoration: none;
                opacity: 0.8;
                font-style: italic;
                line-height: 24px;
                letter-spacing: 0.9px;
                margin-right: 29px;
                color: #000000;
            }

            a:hover {
                transition: all 0.5s;
                color: red;
            }

            .products-infos {

                display: flex;
                flex-direction: row;
                justify-content: space-between;
                flex-wrap: wrap;
                padding-top: 67px;

                .icon-1 {
                    background-image: url('../img/people/9.png');
                    background-position: center center;
                    background-repeat: no-repeat;
                    width: 150px;
                    height: 150px;
                }
                .icon-2 {
                    background-image: url('../img/people/10.png');
                    background-position: center center;
                    background-repeat: no-repeat;
                    width: 150px;
                    height: 150px;
                }
                .icon-3 {
                    background-image: url('../img/people/11.png');
                    background-position: center center;
                    background-repeat: no-repeat;
                    width: 150px;
                    height: 150px;
                }
                .icon-4 {
                    background-image: url('../img/people/13.png');
                    background-position: center center;
                    background-repeat: no-repeat;
                    width: 150px;
                    height: 150px;
                }
                &-icon {
                    width: 150px;
                    height: 150px;
                    position: relative;
                    
                    
                    span {
                        visibility: hidden;
                        display: block;
                        background-color: #000000;
                        opacity: 0.7;
                        color: #ffffff;
                        font-size: 18px;
                        text-transform: uppercase;
                        letter-spacing: 0.17px;
                        line-height: 27px;
                        width: 150px;
                        height: 27px; 
                        text-align: center;
                        position: absolute;
                        left: 0;
                        bottom: 0;
                    }
                }
                &-icon:hover {
                    cursor: pointer;

                    span {
                        visibility: visible;
                    }
                }
            }
            
        }
    
}

.sub {
    background-color: #f5f0dd;
    margin-top: 110px;

    &-all {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-bottom: 140px;

        h1 {
            opacity: 0.9;
            font-size: 42px;
            font-weight: 700;
            line-height: 24px;
            letter-spacing: 1.05px;
            padding-top: 140px;
        }

        p{
            font-size: 16px;
            line-height: 28px;
            padding-top: 31px;
            padding-bottom: 62px;

        }

        &-input {
            padding: 20px 363px 20px 36px;

        }

        &-btn {
            padding: 20px 57px;
            text-transform: uppercase;
            
        }
    }
}